<template>
  <div>
    <LightTopBar title="Edit Profile" :linked="true" />
    <div class="container">
      <form @submit.prevent="saveDet">
        <div class="mt-5">
          <md-field class="mb-4">
            <label>First name</label>
            <md-input v-model="FirstName"></md-input>
          </md-field>
          <md-field class="mb-4">
            <label>Last name</label>
            <md-input v-model="LastName"></md-input>
          </md-field>
          <md-field class="mb-4">
            <label>Username</label>
            <md-input required v-model="username"></md-input>
          </md-field>
          <md-field class="mb-4">
            <label>Mobile Number</label>
            <md-input v-model="phonenumber"></md-input>
          </md-field>
          <md-field class="mb-4">
            <label>Email address</label>
            <md-input required v-model="emailAddress" type="email"></md-input>
          </md-field>
          <md-field>
            <label>Bio</label>
            <md-textarea class="pl-0" v-model="UserBio" md-counter="95"></md-textarea>
          </md-field>
        </div>
        <div class="mt-5 text-center">
          <md-button type="submit" :disabled="loading" class="md-primary md-round mb-2 md-block">
            <img class="loadingimg" v-if="loading" height="10" src="/img/loader.svg" alt />
            <span v-else>Update Profile</span>
          </md-button>
        </div>
      </form>
    </div>
  </div>
</template>
<script>
import LightTopBar from "../components/LightTopBar";

export default {
  components: {
    LightTopBar
  },
  data() {
    return {
      userData: {},
      FirstName: "",
      LastName: "",
      UserBio: "",
      loading: false,
      username: "",
      emailAddress: "",
      phonenumber: "",
      password: "",
      confirmPassword: ""
    };
  },
  computed: {
    notificationSystem() {
      return this.$store.getters.notificationSystem;
    }
  },
  mounted() {
    this.userData = JSON.parse(localStorage.getItem("user"));
    this.UserBio = this.userData.userBio;
    this.FirstName = this.userData.firstName;
    this.LastName = this.userData.lastName;
    this.username = this.userData.username;
    this.emailAddress = this.userData.emailAddress;
    this.phonenumber = this.userData.phonenumber;
  },
  methods: {
    saveDet() {
      let user = {
        EmailAddress: this.emailAddress,
        FirstName: this.FirstName,
        LastName: this.LastName,
        Phonenumber: this.phonenumber,
        Username: this.username,
        UserBio: this.UserBio
      };
      console.log(user);
      this.loading = true;
      this.$store
        .dispatch("updateProfile", user)
        .then(resp => {
          console.log(resp);
          localStorage.setItem("user", JSON.stringify(resp.data));
          if (resp.message == "") {
            console.log("Empty Message");
            this.$swal({
              icon: "success",
              text: "Profile Updated",
              confirmButtonColor: "#36467b"
            }).then(result => {
              if (result.value) {
                this.$router.push("user-profile");
              }
            });
          } else {
            console.log(resp.message);
            localStorage.setItem("userphone", this.phonenumber);
            this.$swal({
              icon: "info",
              text: resp.message,
              showCancelButton: true,
              confirmButtonColor: "#36467b",
              cancelButtonColor: "#fe900b",
              confirmButtonText: "Verify",
              cancelButtonText: "Skip"
            }).then(result => {
              if (result.value) {
                this.$router.push("verify-phone");
              }
            });
          }
          this.loading = false;
          // this.$router.push("user-profile");
        })
        .catch(err => {
          console.log(err);
          this.loading = false;

          if (err.status == 400) {
            this.$swal({
              icon: "info",
              text: err.data.message,
              confirmButtonColor: "#36467b"
            });
          } else {
            this.$swal({
              icon: "info",
              text: "Unable to update Profile, Please try again!",
              confirmButtonColor: "#36467b"
            });
          }
        });
    }
  }
};
</script>

<style lang="css"></style>
