<template>
  <div>
    <LightTopBar title="Register Account" />
    <form @submit.prevent="register">
      <div class="container">
        <div class="mt-5">
          <md-field class="mb-4">
            <label>Full name</label>
            <md-input required v-model="fullname"></md-input>
          </md-field>
          <md-field class="mb-4">
            <label>Store name</label>
            <md-input required v-model="storeName"></md-input>
          </md-field>
          <md-field class="mb-4">
            <label>Store address</label>
            <md-input required v-model="storeAddress"></md-input>
          </md-field>
          <md-field class="mb-4">
            <label>Mobile Number</label>
            <md-input required v-model="phonenumber"></md-input>
          </md-field>
          <md-field class="mb-4">
            <label>Email address</label>
            <md-input required v-model="emailAddress" type="email"></md-input>
          </md-field>
          <md-field class="mb-4" md-clearable>
            <label>Password</label>
            <md-input required v-model="password" type="password"></md-input>
          </md-field>
          <!-- <md-field class="mb-4">
            <label>Referral Code</label>
            <md-input ></md-input>
            <span class="md-helper-text"
              >Enter code that you got referred from</span
            >
          </md-field> -->
        </div>
        <div class="movdown text-center">
          <md-button
            type="submit"
            :disabled="loading"
            class="md-primary md-round mb-2 md-block"
          >
            <img
              class="loadingimg"
              v-if="loading"
              height="10"
              src="/img/loader.svg"
              alt
            />
            <span v-else>Sign Up</span>
          </md-button>
          <p class="mt-3">
            Already have an account?
            <router-link to="/Login" class="text-info" href="#"
              >Sign in</router-link
            >
          </p>
        </div>
      </div>
    </form>
  </div>
</template>
<script>
import LightTopBar from "../components/LightTopBar";

export default {
  components: {
    LightTopBar,
  },
  data() {
    return {
      fullname: "",
      storeName: "",
      storeAddress: "",
      emailAddress: "",
      phonenumber: "",
      password: "",
      loading: false,
    };
  },
  computed: {
    notificationSystem() {
      return this.$store.getters.notificationSystem;
    },
  },
  methods: {
    register() {
      let user = {
        userClass: "Seller",
        emailAddress: this.emailAddress,
        fullname: this.fullname,
        phonenumber: this.phonenumber,
        userBio: "",
        password: this.password,
        confirmPassword: this.password,
        storeName: this.storeName,
        storeAddress: this.storeAddress,
        verifyOnEmail: true,
        verifyOnPhonenumber: false,
      };
      // console.log(user);
      this.loading = true;
      this.$store
        .dispatch("registerUser", user)
        .then((resp) => {
          this.$toast.success(
            "Sign Up Successful",
            "Your account has been created!",
            this.notificationSystem.options.success
          );
          this.loading = false;
          localStorage.setItem("regEmail", this.emailAddress);
          this.$router.push("vendor-registeration-complete");
          //  vendor-registeration-complete
        })
        .catch((err) => {
          this.loading = false;
          if (err.status == 400) {
            this.$toast.info(
              "Sign up failed!",
              err.data.message,
              this.notificationSystem.options.info
            );
          } else {
            this.$toast.error(
              "Please try again!",
              "Sign in Failed",
              this.notificationSystem.options.error
            );
          }
        });

      // to="/register-vendor-bank"
    },
  },
};
</script>

<style lang="css"></style>
