<template>
  <div>
    <LightTopBar title="Register Account" />
    <div class="container">
      <form @submit.prevent="saveDet">
        <div class="mt-5">
          <md-field class="mb-4">
            <label>First name</label>
            <md-input required v-model="firstName"></md-input>
          </md-field>
          <md-field class="mb-4">
            <label>Last name</label>
            <md-input required v-model="lastName"></md-input>
          </md-field>
          <md-field class="mb-4">
            <label>Username</label>
            <md-input required v-model="username"></md-input>
          </md-field>
          <md-field class="mb-4">
            <label>Mobile Number</label>
            <md-input required v-model="phonenumber"></md-input>
          </md-field>
          <md-field class="mb-4">
            <label>Email address</label>
            <md-input required v-model="emailAddress" type="email"></md-input>
          </md-field>
          <md-field class="mb-4" md-clearable>
            <label>Password</label>
            <md-input required v-model="password" type="password"></md-input>
          </md-field>
          <!-- <md-field class="mb-4">
          <label>Referral Code</label>
          <md-input></md-input>
          <span class="md-helper-text">Enter code that you got referred from</span>
          </md-field>-->
        </div>
        <div class="movdown text-center">
          <md-button
            type="submit"
            :disabled="loading"
            class="md-primary md-round mb-2 md-block"
          >
            <img
              class="loadingimg"
              v-if="loading"
              height="10"
              src="/img/loader.svg"
              alt
            />
            <span v-else>Sign Up</span>
          </md-button>
          <p class="mt-3">
            Already have an account?
            <router-link to="/Login" class="text-info" href="#"
              >Sign in</router-link
            >
          </p>
        </div>
      </form>
    </div>
  </div>
</template>
<script>
import LightTopBar from "../components/LightTopBar";

export default {
  components: {
    LightTopBar,
  },
  data() {
    return {
      firstName: "",
      lastName: "",
      loading: false,
      username: "",
      emailAddress: "",
      phonenumber: "",
      password: "",
      confirmPassword: "",
    };
  },
  computed: {
    notificationSystem() {
      return this.$store.getters.notificationSystem;
    },
  },
  methods: {
    saveDet() {
      let user = {
        userClass: "Buyer",
        emailAddress: this.emailAddress,
        firstName: this.firstName,
        lastName: this.lastName,
        phonenumber: this.phonenumber,
        interests: JSON.parse(localStorage.getItem("selectedInterest")),
        username: this.username,
        userBio: "",
        password: this.password,
        confirmPassword: this.password,
        storeName: "",
        storeAddress: "",
        verifyOnEmail: true,
        verifyOnPhonenumber: false,
      };
      console.log(user);
      this.loading = true;
      this.$store
        .dispatch("registerUser", user)
        .then((resp) => {
          this.$toast.success(
            "Sign Up Successful",
            "Your account has been created!",
            this.notificationSystem.options.success
          );
          this.loading = false;
          localStorage.setItem("regEmail", this.emailAddress);
          this.$router.push("user-registeration-complete");
          //  vendor-registeration-complete
        })
        .catch((err) => {
          this.loading = false;
          if (err.status == 400) {
            this.$toast.info(
              "Sign up failed!",
              err.data.message,
              this.notificationSystem.options.info
            );
          } else {
            this.$toast.error(
              "Please try again!",
              "Sign in Failed",
              this.notificationSystem.options.error
            );
          }
        });
    },
  },
};
</script>

<style lang="css"></style>
