<template>
  <div>
    <LightTopBar title="Welcome" />
    <div class="container">
      <h3 class="mt-5 mb-3 text-center">Who are you?</h3>

      <div class="movdown text-center">
        <div class="mb-4">
          <md-button
            @click="setRole('Seller')"
            class="md-info md-round mb-2 md-block"
            >I am a Vendor</md-button
          >
          <md-button
            @click="setRole('Buyer')"
            class="md-primary md-round mb-2 md-block"
            >I am a User</md-button
          >
        </div>
        <p class="mt-5">
          Already have an account?
          <router-link to="/login" class="text-info" href="#"
            >Sign in</router-link
          >
        </p>
      </div>
    </div>
  </div>
</template>
<script>
import LightTopBar from "../components/LightTopBar";

export default {
  components: {
    LightTopBar,
  },
  data() {
    return {
      firstname: null,
      email: null,
      password: null,
    };
  },
  methods: {
    setRole(role) {
      console.log(role);
      this.$store.commit("setUserRole", role);
      if (role == "Buyer") {
        localStorage.setItem("userRole", "Buyer");
        this.$router.push({ path: "register-user" });
      } else {
        localStorage.setItem("userRole", "Seller");
        this.$router.push({ path: "register-vendor" });
      }
    },
  },
};
</script>

<style lang="css"></style>
