import axios from 'axios';

const baseURL = 'http://inasync-dev.eu-west-2.elasticbeanstalk.com/api';

export const actions = {
    getInterests({
        commit
    }) {
        return new Promise((resolve, reject) => {
            axios({
                    url: baseURL + '/Auth/GetInterests',
                    method: 'GET'
                })
                .then(resp => {
                    // console.log(resp.data.data)
                    commit('setInterest', resp.data.data);
                    resolve(resp.data);
                })
                .catch(err => {
                    reject()
                });
        });
    },
    login({
        commit,
    }, user) {
        return new Promise((resolve, reject) => {
            commit('auth_request');
            axios({
                    url: baseURL + '/Auth/LoginEmail',
                    data: user,
                    method: 'POST'
                })
                .then(resp => {

                    // console.log(resp.headers['set-auth']);
                    const token = resp.headers['set-auth'];
                    const user = resp.data.data;
                    localStorage.setItem('token', token);
                    localStorage.setItem('user', JSON.stringify(user));

                    axios.defaults.headers.common['Authorization'] = token;
                    commit('auth_success', token, user);

                    // dispatch('getUser');
                    resolve(resp.data);
                })
                .catch(err => {
                    commit('auth_error');
                    localStorage.removeItem('token');
                    if (err.response) {
                        if (err.response.status)
                            reject(err.response);
                    } else {
                        reject();

                    }
                });
        });
    },
    forgotPassword({
        commit
    }, user) {
        return new Promise((resolve, reject) => {
            commit('auth_request');
            console.log(user.email);
            var emailData = new FormData();
            emailData.append('email', user.email);
            let userEmail = emailData.get('email');

            axios({
                    url: baseURL + '/accounts/password-reset/',
                    data: userEmail,
                    method: 'POST'
                })
                .then(resp => {
                    const token = resp.data.token;
                    const user = resp.data.user;
                    localStorage.setItem('token', token);
                    axios.defaults.headers.common['Authorization'] = token;
                    commit('auth_success', token, user);
                    commit('loading', false);
                    resolve(resp);
                })
                .catch(err => {
                    commit('auth_error');
                    commit('loading', false);
                    localStorage.removeItem('token');
                    reject(err);
                });
        });
    },
    registerUser({
        commit,
    }, user) {
        return new Promise((resolve, reject) => {
            commit('auth_request');
            axios({
                    url: baseURL + '/Auth/Register',
                    data: user,
                    method: 'POST'
                })
                .then(resp => {
                    // console.log(resp)
                    // console.log(resp.headers['set-auth']);
                    // const token = resp.headers['set-auth'];
                    // const user = resp.data.data;
                    // localStorage.setItem('token', token);
                    // localStorage.setItem('user', JSON.stringify(user));

                    // axios.defaults.headers.common['Authorization'] = token;
                    // commit('auth_success', token, user);

                    // // dispatch('getUser');
                    resolve(resp.data);
                })
                .catch(err => {
                    // console.log(err)
                    commit('auth_error');
                    localStorage.removeItem('token');
                    if (err.response) {
                        // console.log(err.response)
                        reject(err.response)
                    } else {
                        reject();
                    }
                });
        });
    },

    updateProfile({
        commit,
    }, user) {
        return new Promise((resolve, reject) => {
            let userToken = localStorage.getItem('token');
            axios({
                    url: baseURL + '/User/UpdateProfile',
                    data: user,
                    method: 'POST',
                    headers: {
                        'Content-Type': 'application/json',
                        Authorization: 'Bearer ' + userToken
                    }
                })
                .then(resp => {
                    resolve(resp.data);
                })
                .catch(err => {
                    if (err.response) {
                        // console.log(err.response)
                        reject(err.response)
                    } else {
                        reject();
                    }
                });
        });
    },

    UpdateProfilePicture({
        commit,
    }, file) {
        return new Promise((resolve, reject) => {
            console.log(file)
            let filedata = new FormData()
            filedata.append('file', file)
            let userToken = localStorage.getItem('token');
            axios({
                    url: baseURL + '/User/UpdateProfilePicture',
                    data: filedata,
                    method: 'POST',
                    headers: {
                        'Content-Type': 'application/json',
                        Authorization: 'Bearer ' + userToken
                    }
                })
                .then(resp => {
                    resolve(resp.data);
                })
                .catch(err => {
                    if (err.response) {
                        // console.log(err.response)
                        reject(err.response)
                    } else {
                        reject();
                    }
                });
        });
    },

    verifyCode({
        commit,
    }, code) {
        return new Promise((resolve, reject) => {
            let userToken = localStorage.getItem('token');
            axios({
                    url: baseURL + '/Auth/VerifyUserPhonenumber/' + code,
                    method: 'GET',
                    headers: {
                        'Content-Type': 'application/json',
                        Authorization: 'Bearer ' + userToken
                    }
                })
                .then(resp => {
                    resolve(resp.data);
                })
                .catch(err => {
                    if (err.response) {
                        if (err.response.status) {
                            reject(err.response);
                        }

                    } else {
                        reject();

                    }

                });
        });
    },
    resendCode({
        commit,
    }) {
        return new Promise((resolve, reject) => {
            let userToken = localStorage.getItem('token');
            axios({
                    url: baseURL + '/User/ResendPhonenumberVerificationCode',
                    method: 'GET',
                    headers: {
                        'Content-Type': 'application/json',
                        Authorization: 'Bearer ' + userToken
                    }
                })
                .then(resp => {
                    resolve(resp.data);
                })
                .catch(err => {
                    if (err.response) {
                        if (err.response.status) {
                            reject(err.response);
                        }

                    } else {
                        reject();

                    }

                });
        });
    },
    logout({
        commit
    }) {
        return new Promise((resolve, reject) => {
            commit('logout');
            localStorage.removeItem('token');
            localStorage.removeItem('user');

            delete axios.defaults.headers.common['Authorization'];
            resolve();
        });
    }
};