<template>
  <div>
    <LightTopBar title="Register Account" />
    <div class="container">
      <div v-if="loadingInt" class="text-center mt-5">
        <img class="loadingtheme" height="10" src="/img/loader-theme.svg" alt />
        <p>Loading Interests</p>
      </div>
      <div v-else>
        <h3>Select your interest</h3>
        <div class="circleInterest">
          <div
            v-for="(interest, index) in interestDyn"
            :key="index"
            @click="selectInterest(interest.title)"
            :class="{ selected: interest.selected }"
          >
            <p>{{ interest.title.interestName }}</p>
          </div>
        </div>

        <div class="mt-5">
          <md-button @click="continueReg" class="md-primary md-round mb-2 md-block">Continue</md-button>
        </div>
        <div class="movdown text-center">
          <p class>
            Have an account?
            <router-link to="/login" class="text-info" href="#">Sign in</router-link>
          </p>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import LightTopBar from "../components/LightTopBar";

export default {
  components: {
    LightTopBar
  },
  computed: {
    interests() {
      return this.$store.getters.allInterests;
    }
  },
  data() {
    return {
      loadingInt: true,
      firstname: null,
      email: null,
      password: null,
      remember: true,
      interestDyn: []
      // interests: [
      //   { title: "Scuba Diving", selected: false },
      //   { title: "Camping", selected: false },
      //   { title: "Skydiving", selected: false },
      //   { title: "Surfing", selected: false },
      //   { title: "Skiing", selected: false },
      //   { title: "Swimming", selected: false },
      //   { title: "Drawing", selected: false },
      //   { title: "Painting", selected: false },
      // ],
    };
  },
  created() {
    this.$store
      .dispatch("getInterests")
      .then(resp => {
        this.interestDyn = this.interests.map(interest => ({
          title: interest,
          selected: false
        }));
        this.loadingInt = false;
      })
      .catch(err => {
        this.loadingInt = false;
      });
  },
  methods: {
    selectInterest(title) {
      let interestIndex = this.interestDyn.findIndex(
        interest => interest.title == title
      );
      this.interestDyn[interestIndex].selected = !this.interestDyn[
        interestIndex
      ].selected;
    },
    continueReg() {
      let interestSelected = this.interestDyn.filter(function(el) {
        return el.selected == true;
      });
      interestSelected = interestSelected.map(a => a.title.interestId);
      // console.log(interestSelected);
      localStorage.setItem(
        "selectedInterest",
        JSON.stringify(interestSelected)
      );
      this.$store.commit("saveInterests", interestSelected);
      this.$router.push({ path: "register-user-mail" });
    }
  }
};
</script>

<style lang="css"></style>
