export const mutations = {
    auth_request(state) {
        state.status = 'loading';
    },
    auth_success(state, token, user) {
        state.status = 'success';
        state.token = token;
        state.user = user;
    },
    auth_error(state) {
        state.status = 'error';
    },
    logout(state) {
        state.status = '';
        state.token = '';
    },
    loading(state, isLoading) {
        if (isLoading) {
            state.isLoading = true;
        } else {
            state.isLoading = false;
        }
    },
    setloadingBtn(state, loadingStatus) {
        state.loadingbtn = loadingStatus
    },
    setUserRole(state, role) {
        state.userRegister.userClass = role
    },
    setInterest(state, interests) {
        state.interests = interests
    },
    saveInterests(state, interests) {
        state.userRegister.interests = interests
    },
    saveDetails(state, details) {
        state.userRegister.emailAddress = details.emailAddress
        state.userRegister.fullname = details.fullname
        state.userRegister.username = details.username
        state.userRegister.password = details.password
        state.userRegister.password = details.password
        state.userRegister.confirmPassword = details.confirmPassword
    },
    savePhone(state, phone) {
        state.userRegister.phonenumber = phone
    },
};