<template>
  <div>
    <LightTopBar title="Edit Profile Image" :linked="true" />
    <div class="container">
      <form @submit.prevent="saveDet">
        <div class="mt-5">
          <div class="editProfilImg" :style="'background: url(' +profilePic + ');'"></div>
          <img id="output" />
        </div>
        <div class="mt-5 text-center">
          <!-- <md-button class="md-info md-round mb-3">
            <span>Replace Image</span>
          </md-button>-->
          <label
            for="file-upload"
            class="md-button custom-file-upload md-info md-round mb-3"
          >Replace Image</label>
          <input
            class="fileUploadd"
            id="file-upload"
            type="file"
            accept="image/png, image/jpeg, image/jpg"
            @change="handleFileChange($event)"
          />
          <br />
          <md-button type="submit" :disabled="loading" class="md-primary md-round mb-2">
            <img class="loadingimg" v-if="loading" height="10" src="/img/loader.svg" alt />
            <span v-else>Save Image</span>
          </md-button>
        </div>
      </form>
    </div>
  </div>
</template>
<script>
import LightTopBar from "../components/LightTopBar";

export default {
  components: {
    LightTopBar
  },
  data() {
    return {
      userData: {},
      profilePic: "",
      imgURL: "",
      imageFile: "",
      loading: false
    };
  },
  computed: {
    notificationSystem() {
      return this.$store.getters.notificationSystem;
    }
  },
  mounted() {
    this.userData = JSON.parse(localStorage.getItem("user"));
    if (this.userData.profilePic == null) {
      this.profilePic = require("@/assets/img/app-img/default.png");
    } else {
      this.profilePic = this.userData.profilePic;
    }
  },
  methods: {
    handleFileChange: function(event) {
      console.log(event.target.files[0]);
      this.imageFile = event.target.files[0];
      this.profilePic = window.URL.createObjectURL(event.target.files[0]);
    },
    saveDet() {
      this.loading = true;
      let file = this.imageFile;
      this.$store
        .dispatch("UpdateProfilePicture", file)
        .then(resp => {
          this.loading = false;
          console.log(resp);
          let userdata = JSON.parse(localStorage.getItem("user"));
          userdata.profilePic = resp.data;
          localStorage.setItem("user", JSON.stringify(userdata));
          this.$swal({
            icon: "success",
            text: "Profile Image Updated",
            confirmButtonColor: "#36467b"
          }).then(result => {
            if (result.value) {
              this.$router.push("user-profile");
            }
          });
        })
        .catch(err => {
          this.loading = false;
          console.log(err);
          if (err.status == 400) {
            this.$swal({
              icon: "info",
              text: err.data.message,
              confirmButtonColor: "#36467b"
            });
          } else {
            this.$swal({
              icon: "info",
              text: "Unable to update Profile Image, Please try again!",
              confirmButtonColor: "#36467b"
            });
          }
        });
    }
  }
};
</script>

<style lang="css"></style>
