<template>
  <div>
    <LightTopBar title="Verify Phone" :linked="true" />
    <div class="container">
      <div class="text-center my-5">
        <img src="@/assets/img/app-img/phone.svg" />
      </div>
      <h3>Verify Phone Number</h3>
      <p>We sent you a code to verify your phone number</p>
      <p class="text-info">Sent to {{ userMobile }}</p>
      <form @submit.prevent="verifyCode">
        <div class="mt-5">
          <md-field class="mb-4">
            <label>Code</label>
            <md-input required v-model="code"></md-input>
          </md-field>
        </div>
        <div class="movdown text-center">
          <md-button
            :disabled="code.length < 1 || loading"
            type="submit"
            class="md-primary md-round mb-2 md-block"
          >
            <img class="loadingimg" v-if="loading" height="10" src="/img/loader.svg" alt />
            <span v-else>Verify</span>
          </md-button>
          <p class="mt-2">
            Didn't get the code?
            <a class="text-info" @click="resendCode()">Resend</a>
          </p>
        </div>
      </form>
    </div>
  </div>
</template>
<script>
import LightTopBar from "../components/LightTopBar";

export default {
  components: {
    LightTopBar
  },
  computed: {
    userMobile() {
      return localStorage.getItem("userphone");
    }
  },
  data() {
    return {
      code: "",
      loading: false
    };
  },
  methods: {
    resendCode() {
      console.log("resendcode");
      this.$store
        .dispatch("resendCode")
        .then(resp => {
          console.log(resp);
          this.$swal({
            icon: "success",
            text: "Code Sent! Check your Inbox",
            confirmButtonColor: "#36467b"
          });
        })
        .catch(err => {
          this.loading = false;
          if (err.status == 400) {
            this.$swal({
              icon: "info",
              text: err.data.message
            });
          } else {
            this.$swal({
              icon: "info",
              text: "'Error while sending code, Please try again!'"
            });
          }
        });
    },
    verifyCode() {
      this.loading = true;
      let code = this.code;
      this.$store
        .dispatch("verifyCode", code)
        .then(resp => {
          console.log(resp);
          this.code = "";
          this.$swal({
            icon: "success",
            text: "Phone Number Verified",
            confirmButtonColor: "#36467b"
          }).then(result => {
            if (result.value) {
              this.$router.push("user-profile");
            }
          });
        })
        .catch(err => {
          this.loading = false;
          if (err.status == 400) {
            this.$swal({
              icon: "info",
              text: "Invalid Code, Please try again!"
            });
          } else {
            this.$swal({
              icon: "info",
              text: "'Error while sending code, Please try again!'"
            });
          }
        });
      //   this.$router.push({ path: "user-registeration-complete" });
    }
  }
};
</script>

<style lang="css">
.countrysel .md-icon.md-theme-default.md-icon-font {
  display: none !important;
}
.countrysel.md-field:not(.md-disabled):after,
.countrysel.md-field:not(.md-disabled):before {
  background-color: transparent !important;
}
</style>
