<template>
  <div>
    <LightTopBar title="Login" />
    <div class="container">
      <h3>Hello again,</h3>
      <p>
        Welcome back,
        <br />Please sign in to your account
      </p>
      <form @submit.prevent="loginUser">
        <div class="mt-5">
          <md-field class="mb-4" md-clearable>
            <label>Email Address</label>
            <md-input required v-model="email" type="email"></md-input>
          </md-field>
          <md-field class="mb-4">
            <label>Password</label>
            <md-input required v-model="password" type="password"></md-input>
          </md-field>

          <md-checkbox v-model="remember">Remember me</md-checkbox>
        </div>
        <div class="movdown text-center">
          <md-button
            :disabled="btndisable || btnLoadDisable"
            type="submit"
            class="md-primary md-round mb-2 md-block"
          >
            <img
              class="loadingimg"
              v-if="loading"
              height="10"
              src="/img/loader.svg"
              alt
            />
            <span v-else>Sign in</span>
          </md-button>
          <md-button class="md-btnwhite md-round mb-4 md-block">
            <img class="gicon" src="@/assets/img/app-img/google.svg" />Login
            with Google
          </md-button>
          <p class>
            <router-link to="/register" class="text-info" href="#"
              >Sign up</router-link
            >~
            <router-link to="/forgotpassword" class="text-info"
              >Forgot password</router-link
            >
          </p>
        </div>
      </form>
    </div>
  </div>
</template>
<script>
import LightTopBar from "../components/LightTopBar";

export default {
  components: {
    LightTopBar,
  },
  data() {
    return {
      email: "",
      password: "",
      remember: true,
      loadingbtn: false,
      btnLoadDisable: false,
    };
  },
  computed: {
    notificationSystem() {
      return this.$store.getters.notificationSystem;
    },
    btndisable() {
      if (this.email && this.password) {
        return false;
      } else {
        return true;
      }
    },
    loading() {
      return this.$store.getters.loadingStatus;
    },
  },
  methods: {
    loginUser() {
      let user = {
        EmailAddress: this.email,
        Password: this.password,
      };
      let loadingStatus = true;
      this.$store.commit("setloadingBtn", loadingStatus);
      this.btnLoadDisable = true;
      this.$store
        .dispatch("login", user)
        .then((resp) => {
          // console.log(resp);
          if (resp.data.userClassName == "Buyer") {
            this.$router.push({ path: "user-app" });
            localStorage.setItem("role", "user-app");
          } else if (resp.data.userClassName == "Seller") {
            this.$router.push({ path: "vendor-app" });
            localStorage.setItem("role", "vendor-app");
          } else {
          }
          this.$store.commit("setloadingBtn", false);
          this.btnLoadDisable = false;
          this.$toast.success(
            "Redirecting",
            "Sign in Successful!",
            this.notificationSystem.options.success
          );
        })
        .catch((err) => {
          this.$store.commit("setloadingBtn", false);
          this.btnLoadDisable = false;
          if (err.status == 400) {
            this.$toast.error(
              "Sign in Failed",
              err.data.message,
              this.notificationSystem.options.error
            );
          } else {
            this.$toast.error(
              "Please try again!",
              "Sign in Failed",
              this.notificationSystem.options.error
            );
          }
          // console.log(err);
        });
    },
  },
};
</script>

<style lang="css"></style>
